/* app/javascript/stylesheets/application.scss */
@tailwind base;
@tailwind utilities;
@tailwind components;
/* Add any custom CSS here */
@tailwind utilities;
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir=rtl] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.pagy-nav,
.pagy-nav-js {
  @apply flex space-x-2;
}

.pagy-nav .page a,
.pagy-nav .page.active,
.pagy-nav .page.prev.disabled,
.pagy-nav .page.next.disabled,
.pagy-nav-js .page a,
.pagy-nav-js .page.active,
.pagy-nav-js .page.prev.disabled,
.pagy-nav-js .page.next.disabled {
  @apply block rounded-lg px-3 py-1 text-sm text-gray-500 font-semibold bg-gray-200 shadow-md;
}
.pagy-nav .page a:hover,
.pagy-nav .page.active:hover,
.pagy-nav .page.prev.disabled:hover,
.pagy-nav .page.next.disabled:hover,
.pagy-nav-js .page a:hover,
.pagy-nav-js .page.active:hover,
.pagy-nav-js .page.prev.disabled:hover,
.pagy-nav-js .page.next.disabled:hover {
  @apply bg-gray-300;
}
.pagy-nav .page a:active,
.pagy-nav .page.active:active,
.pagy-nav .page.prev.disabled:active,
.pagy-nav .page.next.disabled:active,
.pagy-nav-js .page a:active,
.pagy-nav-js .page.active:active,
.pagy-nav-js .page.prev.disabled:active,
.pagy-nav-js .page.next.disabled:active {
  @apply bg-gray-400 text-white;
}

.pagy-nav .page.prev.disabled,
.pagy-nav .page.next.disabled,
.pagy-nav-js .page.prev.disabled,
.pagy-nav-js .page.next.disabled {
  @apply text-gray-400 cursor-default;
}
.pagy-nav .page.prev.disabled:hover,
.pagy-nav .page.next.disabled:hover,
.pagy-nav-js .page.prev.disabled:hover,
.pagy-nav-js .page.next.disabled:hover {
  @apply text-gray-400 bg-gray-200;
}
.pagy-nav .page.prev.disabled:active,
.pagy-nav .page.next.disabled:active,
.pagy-nav-js .page.prev.disabled:active,
.pagy-nav-js .page.next.disabled:active {
  @apply text-gray-400 bg-gray-200;
}

.pagy-nav .page.active,
.pagy-nav-js .page.active {
  @apply text-white cursor-default bg-gray-400;
}
.pagy-nav .page.active:hover,
.pagy-nav-js .page.active:hover {
  @apply text-white bg-gray-400;
}
.pagy-nav .page.active:active,
.pagy-nav-js .page.active:active {
  @apply bg-gray-400 text-white;
}

.pagy-combo-nav-js {
  @apply flex max-w-max rounded-full px-3 py-1 text-sm text-gray-500 font-semibold bg-gray-200 shadow-md;
}

.pagy-combo-nav-js .pagy-combo-input {
  @apply bg-white px-2 rounded-sm;
}

.pagy-combo-nav-js .page.prev:hover,
.pagy-combo-nav-js .page.next:hover {
  @apply text-gray-800;
}
.pagy-combo-nav-js .page.prev:active,
.pagy-combo-nav-js .page.next:active {
  @apply text-gray-800;
}

.pagy-combo-nav-js .page.prev.disabled,
.pagy-combo-nav-js .page.next.disabled {
  @apply text-gray-400 cursor-default;
}